import React from 'react';
import clsx from 'clsx';
import { Link, LinkMode, LinkTarget } from '@zepdev/design-system-component-library-react';
import { LinkListItemProps } from '../link-list-item';

export const LinkListItem: React.FC<LinkListItemProps> = ({ headline, description, theme = 'power-systems', links }) => {
  const colorByThemeClass = theme === 'power-systems' ? 'zep-text-primary-default' : 'zep-text-typography-dark-100';

  return (
    <div
      className={clsx(
        'zep-flex',
        'zep-gap-1.5',
        'zep-p-1.5',
        'sm:zep-p-2',
        'md:zep-p-2.5',
        'zep-1.5',
        'zep-flex-col',
        'zep-items-start',
        'zep-max-w-[774px]',
        'zep-min-w-[290px]',
        'zep-bg-background-medium'
      )}
      data-testid="zep-link-list"
    >
      {headline && (
        <h4
          data-testid="link-list-headline"
          className={clsx('zep-typography-headlineSM-fluid-cqi', 'zep-break-all', colorByThemeClass)}
        >
          {headline}
        </h4>
      )}
      {description && (
        <p
          data-testid="link-list-description"
          className={clsx('zep-typography-bodyText', 'zep-break-all', colorByThemeClass)}
        >
          {description}
        </p>
      )}
      <div
        data-testid="link-list-links"
        className={clsx('zep-flex', 'zep-flex-col', 'zep-gap-1', colorByThemeClass)}
      >
        {links.map((link, index) => (
          <Link
            key={index}
            label={link.label}
            icon={link.icon}
            href={link.href}
            iconPlacement="before"
            hasIcon
            target={LinkTarget.Blank}
            mode={LinkMode.Inline}
          />
        ))}
      </div>
    </div>
  );
}
